	// import topTab from "../../components/top-tab.vue";
	export default {
		data: {
			tabs: [{
				name: "全部",
				active: true,
				status: 0,
				page: 0,
				loading: false
			}, {
				name: "待受理",
				active: false,
				status: 1,
				page: 0,
				loading: false
			}, {
				name: "已受理",
				active: false,
				status: 2,
				page: 0,
				loading: false
			}, {
				name: "处理中",
				active: false,
				status: 10,
				page: 0,
				loading: false
			}, {
				name: "已完成",
				active: false,
				status: 3,
				page: 0,
				loading: false
			}],
			content_height: 0,
			taskList: [],
			selectedIndex: 0
		},
		onLoad(e) {
			var _this = this;
			var ts = [];
			for (var i in this.tabs) {
				ts.push([]);
			}
			this.taskList = ts;
			switch (e.type) {
				case 1:
					this.tag = "task-status status-doing";
					this.status_str = "处理中"
					break;
				case 2:
					this.tag = "task-status status-finish";
					this.status_str = "已处理"
					break;
			}
			uni.getSystemInfo({
				success: function(res) {
					_this.content_height = res.windowHeight - 36;
				}
			})
			this.getList(0);
		},
		methods: {
			switchTab(index) {
				this.selectedIndex = index;
				for (var i in this.tabs) {
					this.tabs[i].active = (i == index + "");
				}
				this.getList(index);
			},
			lower: function(e) {
				this.getList(parseInt(e.currentTarget.id.replace("tab", "")));
			},
			getList(tab) {
				var _this = this;
				(function(tab) {
					utils.requestGet("operation/getmodifylist/" + _this.tabs[tab].page + "/" + _this.tabs[tab].status,
						function(res) {
							if (res.code == 0) {
								var data = res.data;
								if (data.length > 0) {
									_this.tabs[tab].page++;
									var status_str = "";
									for (var j in data) {
										data[j].ut = utils.formatTimeStr(data[j].ut);
										switch (data[j].status) {
											case 0:
												data[j].status_str = "待受理";
												data[j].tag = "undo";
												break;
											case 1:
												data[j].status_str = "已受理";
												data[j].tag = "doing";
												break;
											case 2:
												data[j].status_str = "已完成";
												data[j].tag = "finish";
												break;
											case 3:
												data[j].status_str = "误报";
												data[j].tag = "finish";
												break;
											case 9:
												data[j].status_str = "处理中";
												data[j].tag = "doing";
												break;
											default:
												data[j].status_str = "";
												break
										}
									}
									var list = _this.taskList[tab];
									var newList = list.concat(data);
									_this.taskList.splice(tab, 1, newList);
								}
								//_this.list = res.data;
							}
						})
				})(tab)

			},
			goDetail(e) {
				var dataset = e.currentTarget.dataset;
				uni.navigateTo({
					url: '../detail/detail?school_id=' + dataset.sl + "&fault_id=" + dataset.fault,
				})
			}
		}
// 		components: {
// 			topTab
// 		}
	}



